/* ─── Brasena Design Tokens (theme-aware) ────────────── */
:root {
  --font-display: 'Playfair Display', serif;
  --font-body:    'DM Sans', sans-serif;
  --font-mono:    'DM Mono', monospace;

  /* Brand */
  --color-brand-black:  #0F1410;
  --color-brand-sage:   #8BAF8E;
  --color-brand-mint:   #A8C5A0;
  --color-brand-forest: #4A7C59;
  --sage:               #8BAF8E;
  --gold-lt:             #D4AE52;
  --gold-bg:             rgba(184, 149, 58, 0.12);
  --gold-border:         rgba(184, 149, 58, 0.25);

  /* Feedback */
  --color-success: #4CAF7D;
  --color-warning: #F0A500;
  --color-error:   #E05C5C;
  --color-info:    #5B9BD5;

  /* Default = dark (deep forest) until data-theme is set */
  --bg:       #080E09;
  --surface:  #111814;
  --card:     #192019;
  --card2:    #1F2820;
  --border:   #2A3A2C;
  --border-lt: rgba(42, 58, 44, 0.6);
  --nav-bg:   rgba(17, 24, 20, 0.95);
  --text:     #EFF6F0;
  --text2:    #8FA48F;
  --text3:    #5A6E5C;
  --hero-bg:  linear-gradient(135deg, #1A2E1C 0%, #111814 60%, #162219 100%);
  --surface-muted: #1A2018;
  --color-surface:        #111814;
  --color-surface-muted:  #1A2018;
  --color-surface-card:   #192019;
  --color-surface-border: #2A3A2C;
  --hero-border: rgba(139, 175, 142, 0.15);
  --text-primary:   #EFF6F0;
  --text-secondary: #8FA48F;
  --color-text-primary:   #EFF6F0;
  --color-text-secondary: #8FA48F;
  --color-text-muted:     #5A6E5C;
  --sage-bg:     rgba(139, 175, 142, 0.12);
  --sage-bg-lt:  rgba(139, 175, 142, 0.18);
  --thumb-bg:    #1F2820;
}

[data-theme="dark"] {
  /* Dark: deep forest green at night — warm, not void */
  --bg:       #080E09;
  --surface:  #111814;
  --card:     #192019;
  --card2:    #1F2820;
  --border:   #2A3A2C;
  --border-lt: rgba(42, 58, 44, 0.6);
  --nav-bg:   rgba(17, 24, 20, 0.95);
  --text:     #EFF6F0;
  --text2:    #8FA48F;
  --text3:    #5A6E5C;
  --hero-bg:  linear-gradient(135deg, #1A2E1C 0%, #111814 60%, #162219 100%);

  --surface-muted: #1A2018;
  --color-surface:        #111814;
  --color-surface-muted:  #1A2018;
  --color-surface-card:   #192019;
  --color-surface-border: #2A3A2C;
  --hero-border: rgba(139, 175, 142, 0.15);
  --text-primary:   #EFF6F0;
  --text-secondary: #8FA48F;
  --color-text-primary:   #EFF6F0;
  --color-text-secondary: #8FA48F;
  --color-text-muted:     #5A6E5C;
  --sage-bg:     rgba(139, 175, 142, 0.12);
  --sage-bg-lt:  rgba(139, 175, 142, 0.18);
  --thumb-bg:    #1F2820;
}

[data-theme="light"] {
  --surface:       #f5f7f5;
  --surface-muted: #e8ede8;
  --card:          #ffffff;
  --card2:         #e8ede8;
  --color-surface:        #f5f7f5;
  --color-surface-muted:  #e8ede8;
  --color-surface-card:   #ffffff;
  --color-surface-border: #d0dcd0;

  --nav-bg:      rgba(245, 247, 245, 0.92);
  --border:      #d0dcd0;
  --border-lt:   rgba(0, 0, 0, 0.08);
  --hero-bg:     linear-gradient(135deg, #e0ede2 0%, #d4e5d6 50%, #c8ddcb 100%);
  --hero-border: rgba(139, 175, 142, 0.35);

  --text-primary:   #0F1410;
  --text-secondary: #3d4a3d;
  --text3:          #627062;
  --color-text-primary:   #0F1410;
  --color-text-secondary: #3d4a3d;
  --color-text-muted:     #627062;

  --sage-bg:     rgba(139, 175, 142, 0.15);
  --sage-bg-lt:  rgba(139, 175, 142, 0.22);
  --thumb-bg:    #e0e8e0;
  --gold-bg:     rgba(184, 149, 58, 0.12);
  --gold-border: rgba(184, 149, 58, 0.3);
}

/* ─── Base Reset ─────────────────────────────────────── */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  background-color: var(--surface);
  color: var(--text-primary);
  font-family: var(--font-body);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ─── App shell (shop: sticky bottom nav inside shell) ─── */
.app-shell {
  max-width: 480px;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: var(--surface);
  position: relative;
}

/* ─── Typography Scale ───────────────────────────────── */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-display);
  font-weight: 600;
  letter-spacing: -0.02em;
  line-height: 1.15;
}

/* ─── Scrollbar ──────────────────────────────────────── */
::-webkit-scrollbar       { width: 6px; height: 6px; }
::-webkit-scrollbar-track { background: var(--color-surface-muted); }
::-webkit-scrollbar-thumb {
  background: var(--color-surface-border);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover { background: var(--color-brand-sage); }

/* ─── Focus States (accessibility) ──────────────────── */
:focus-visible {
  outline: 2px solid var(--color-brand-sage);
  outline-offset: 2px;
  border-radius: 4px;
}

/* ─── Selection ──────────────────────────────────────── */
::selection {
  background: rgba(139, 175, 142, 0.25);
  color: var(--color-text-primary);
}

/* ─── Component Utilities ────────────────────────────── */

/* ─── Animation Utilities ────────────────────────────── */

@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@keyframes slideUp {
  from { opacity: 0; transform: translateY(12px); }
  to   { opacity: 1; transform: translateY(0); }
}
@keyframes pulseRing {
  0%, 100% { opacity: 0.6; transform: scale(0.95); }
  50%      { opacity: 1;   transform: scale(1.05); }
}
.last\:border-b-0:last-child{
  border-bottom-width: 0px;
}
.hover\:border-brand-sage\/40:hover{
  border-color: rgb(139 175 142 / 0.4);
}
.hover\:bg-\[var\(--sage-bg\)\]:hover{
  background-color: var(--sage-bg);
}
.hover\:bg-\[var\(--surface-muted\)\]:hover{
  background-color: var(--surface-muted);
}
.hover\:text-\[var\(--sage\)\]:hover{
  color: var(--sage);
}
.hover\:text-brand-sage:hover{
  --tw-text-opacity: 1;
  color: rgb(139 175 142 / var(--tw-text-opacity, 1));
}
.hover\:text-text-primary:hover{
  --tw-text-opacity: 1;
  color: rgb(245 247 245 / var(--tw-text-opacity, 1));
}
.hover\:text-text-secondary:hover{
  --tw-text-opacity: 1;
  color: rgb(155 173 155 / var(--tw-text-opacity, 1));
}
.hover\:underline:hover{
  text-decoration-line: underline;
}
.hover\:opacity-80:hover{
  opacity: 0.8;
}
.hover\:opacity-90:hover{
  opacity: 0.9;
}
.focus\:border-\[color\:var\(--color-error\)\]:focus{
  border-color: var(--color-error);
}
.focus\:ring-brand-sage:focus{
  --tw-ring-opacity: 1;
  --tw-ring-color: rgb(139 175 142 / var(--tw-ring-opacity, 1));
}
.group:hover .group-hover\:opacity-100{
  opacity: 1;
}
@media (min-width: 640px){

  .sm\:min-w-0{
    min-width: 0px;
  }

  .sm\:grid-cols-3{
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .sm\:flex-row{
    flex-direction: row;
  }

  .sm\:gap-2{
    gap: 0.5rem;
  }

  .sm\:p-8{
    padding: 2rem;
  }

  .sm\:px-4{
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .sm\:px-6{
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }

  .sm\:py-20{
    padding-top: 5rem;
    padding-bottom: 5rem;
  }

  .sm\:text-3xl{
    font-size: 1.95rem;
    line-height: 2.3rem;
  }

  .sm\:text-5xl{
    font-size: 3.25rem;
    line-height: 1.1;
  }

  .sm\:text-7xl{
    font-size: 4.5rem;
    line-height: 1;
  }

  .sm\:text-xl{
    font-size: 1.35rem;
    line-height: 1.85rem;
  }
}
@media (min-width: 1024px){

  .lg\:px-8{
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .lg\:text-6xl{
    font-size: 4rem;
    line-height: 1.05;
  }
}
